@ccclass('Cardinit')
export class Cardinit extends Component {
    // [1]
    // dummy = '';

    // [2]
    @property({type:Prefab})
      public poker=null;
    // serializableDummy = 0;
    init(){
        var node:Node=instantiate(this.poker);
        this.node.addChild(node);
        node.parent=this.node;
        node.setPosition(0,0,0);
        node.active=true;
      }

    start () {
        // [3]
        setTimeout(()=>{this.init();},4500);
    }

    // update (deltaTime: number) {
    //     // [4]
    // }
}

